<template>
  <div class="login">
    <div class="wrapper">
      <h3>Login</h3>
      <div class="logo-login"></div>

      <div v-show="appUpdater.isLatest">
        <font-awesome-icon icon="user" />
        <mt-field class="user-id" placeholder="User Id" type="text" v-model="userId"></mt-field>
        <font-awesome-icon icon="key" />
        <mt-field placeholder="Password" type="password" v-model="password"></mt-field>
        <mt-button
          type="primary"
          size="large"
          @click="onLogin"
        >{{appUpdater.version.length > 0 ? 'Login('+appUpdater.version + ')' : 'Login'}}</mt-button>
      </div>

      <div v-show="!appUpdater.isLatest">
        <div v-show="appUpdater.isAndroid" style="text-align: center;color:white;">
          <mt-button
            v-show="appUpdater.updateError"
            type="primary"
            size="large"
            @click="updateApp"
          >Retry update again</mt-button>

          <h5 style="margin-bottom:0;" v-show="!appUpdater.updateError">{{appUpdater.updateTitle}}</h5>

          <mt-progress :value="appUpdater.downloadProcessValue" :bar-height="5">
            <div slot="start">0%</div>
            <div slot="end">{{appUpdater.downloadProcessValue}}%</div>
          </mt-progress>
        </div>

        <div v-show="!appUpdater.isAndroid">
          <mt-button type="primary" size="large" @click="updateApp">{{appUpdater.updateTitle}}</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Utils from "../utils";
import BizRCHub from "../api/BizRCHub.js";

export default {
  name: "Login",
  data() {
    return {
      userId: "",
      password: "",

      appUpdater: {
        isLatest: false,
        isAndroid: false,

        updateTitle: "Checking new version ...",
        updateBaseUrl: "http://rciupdatetest.azurewebsites.net/RCAppsHome",
        appName: "RCHub",
        version: "2.2.0",
        downloadProcessValue: 0,
        updateError: false
      }
    };
  },
  methods: {
    login(userId, password, ticks) {
      let uuid = "";
      let passwordHash = password;
      if (ticks.length == 0) {
        ticks = Utils.getTicks().toString();
        passwordHash = Utils.encryptPassword(ticks, password);
      }

      this.$showLoading();
      BizRCHub.Login(userId, passwordHash, ticks, uuid)
        .then(res => {
          if (res.data.OK) {
            this.$closeLoading();

            Utils.setRemeberMe(userId, passwordHash, ticks);
            let jobNumber = Utils.getJobNumber();

            let user = res.data.Result;
            let settings = {
              UsrAccountPK: user.UsrAccountPK,
              FirstName: user.FirstName,
              LastName: user.LastName,
              CurrentDateTime: user.CurrentDateTime,
              MinDateTime: user.MinDateTime,
              MaxDateTime: user.MaxDateTime,
              IsCompanyManager: user.IsCompanyManager,
              IsDivisionManager: user.IsDivisionManager,
              IsRegionManager: user.IsRegionManager,
              IsJobManager: user.IsJobManager,
              SearchJobNumber: jobNumber,
              SearchDateTime: user.CurrentDateTime,
              SearchRegionPK: "",
              SearchRegionName: ""
            };

            Utils.setUserToken(userId, passwordHash, ticks, "");

            if (!jobNumber) {
              this.$prompt("Please input job number")
                .then(({ value, action }) => {
                  console.log(action);
                  Utils.setJobNumber(value);
                  settings.SearchJobNumber = value;
                  Utils.setUserSettings(settings);
                  this.$router.push({ name: "JobTimesheetList" });
                })
                .catch(err => {
                  console.log(err);
                  Utils.setUserSettings(settings);
                  this.$router.push({ name: "JobTimesheetList" });
                });
            } else {
              Utils.setUserSettings(settings);
              this.$router.push({ name: "JobTimesheetList" });
            }
          } else {
            BizRCHub.LoginByEmployee("", "", "", userId, password, uuid)
              .then(res => {
                this.$closeLoading();
                if (res.data.OK) {
                  Utils.setRemeberMe(userId, password, ticks);

                  let user = res.data.Result;
                  let settings = {
                    IsEmployee: true,
                    HREmployeePK: user.HREmployeePK,
                    FirstName: user.FirstName,
                    LastName: user.LastName,
                    CurrentDateTime: user.CurrentDateTime,
                    MinDateTime: user.MinDateTime,
                    MaxDateTime: user.MaxDateTime
                  };

                  Utils.setUserToken(
                    user.UserId,
                    user.Password,
                    user.Ticks,
                    user.Secret
                  );
                  Utils.setUserSettings(settings);
                  this.$router.push({ name: "EmployeeTimesheetList" });
                } else {
                  this.$message({ message: res.data.Error, type: "error" });
                  console.log(res.data.Error);
                }
              })
              .catch(err => {
                this.$closeLoading();
                this.showNetworkError();
                console.log(err);
              });
          }
        })
        .catch(err => {
          this.$closeLoading();
          this.showNetworkError();
          console.log(err);
        });
    },
    checkUserInput() {
      if (!this.userId) {
        this.$alert("The user id can not be empty.", "Warning");
        return false;
      }

      if (!this.password) {
        this.$alert("The password can not be empty.", "Warning");
        return false;
      }

      return true;
    },
    onLogin() {
      if (!this.checkUserInput()) return;

      this.login(this.userId, this.password, "");
    },
    autoLogin() {
      let user = Utils.getRemeberMe();
      if (user.UserId.length > 0) {
        this.userId = user.userId;
        this.login(user.UserId, user.Password, user.Ticks);
      }
    },
    checkVersion(callback) {
      var u = navigator.userAgent;
      this.appUpdater.isAndroid =
        u.indexOf("Android") > -1 ||
        u.indexOf("Linux") > -1 ||
        u.indexOf("Chrome") > -1;

      if (this.appUpdater.isAndroid) {
        this.checkAndroidVersion(isUpdate => {
          if (isUpdate) {
            this.updateApp();
          } else {
            callback();
          }
        });
      } else {
        this.checkIOSVersion(isUpdate => {
          if (isUpdate) {
            this.updateApp();
          } else {
            callback();
          }
        });
      }
    },
    checkAndroidVersion(callback) {
      let url = `${this.appUpdater.updateBaseUrl}/${this.appUpdater.appName}/${
        this.appUpdater.appName
      }.txt`;
      axios
        .get(url)
        .then(res => {
          let latestVersion = res.data.Version;
          if (latestVersion != this.appUpdater.version) {
            this.appUpdater.updateTitle = `Update V${
              this.appUpdater.version
            } to V${latestVersion}...`;

            callback(true);
          } else {
            callback(false);
          }
        })
        .catch(err => {
          this.$alert(err);
        });
    },
    checkIOSVersion(callback) {
      let url = `${this.appUpdater.updateBaseUrl}/${this.appUpdater.appName}/${
        this.appUpdater.appName
      }.plist`;
      axios
        .get(url)
        .then(res => {
          let latestVersion = "";
          const splits = res.data.split(/\r\n|[\r\n]/);
          for (let index = 0; index < splits.length; index++) {
            const line = splits[index];
            if (line.endsWith("<key>bundle-version</key>")) {
              if (index + 1 < splits.length) {
                const i = splits[index + 1].indexOf("<string>");
                latestVersion = splits[index + 1].substring(i + 8, i + 8 + 5);
              }
            }
          }

          if (
            latestVersion.length > 0 &&
            latestVersion != this.appUpdater.version
          ) {
            this.appUpdater.updateTitle = `Update V${
              this.appUpdater.version
            } to V${latestVersion}`;

            callback(true);
          } else {
            callback(false);
          }
        })
        .catch(err => {
          this.$alert(err);
        });
    },
    updateApp() {
      if (this.appUpdater.isAndroid) {
        let versionUpdate = new Date().getTime();
        let url = `${this.appUpdater.updateBaseUrl}/${
          this.appUpdater.appName
        }/${this.appUpdater.appName}.apk?v=${versionUpdate}`;

        this.updateAndroid(url);
      } else {
        let url = `${this.appUpdater.updateBaseUrl}/${
          this.appUpdater.appName
        }-update-ipa.html`;

        this.updateIOS(url);
      }
    },
    updateAndroid(url) {
      this.appUpdater.updateError = false;
      this.appUpdater.downloadProcessValue = 0;
      const that = this;
      try {
        window.resolveLocalFileSystemURL(
          // eslint-disable-next-line no-undef
          //LocalFileSystem.PERSISTENT,
          //0,
          // eslint-disable-next-line no-undef
          cordova.file.externalDataDirectory,
          function(dirEntry) {
            dirEntry.getFile(
              `${that.appUpdater.appName}.apk`,
              { create: true, exclusive: false },
              function(fileEntry) {
                axios({
                  method: "GET",
                  url: url,
                  responseType: "blob",
                  onDownloadProgress(progress) {
                    if (progress.lengthComputable) {
                      that.$nextTick(() => {
                        that.appUpdater.downloadProcessValue = Math.round(
                          (progress.loaded / progress.total) * 100
                        );
                      });
                    }
                  }
                })
                  .then(res => {
                    var dataObj = new Blob([res.data], {
                      type: "application/vnd.android.package-archive"
                    });

                    fileEntry.createWriter(function(fileWriter) {
                      fileWriter.onwriteend = function() {
                        // eslint-disable-next-line no-undef
                        cordova.plugins.fileOpener2.open(
                          fileEntry.toInternalURL(),
                          "application/vnd.android.package-archive"
                        );
                      };

                      fileWriter.write(dataObj);
                    });
                  })
                  .catch(err => {
                    alert("download error: " + err);
                    that.appUpdater.updateError = true;
                  });
              },
              err => {
                alert("error getting file! " + err);
                that.appUpdater.updateError = true;
              }
            );
          },
          err => {
            alert("error getting persistent fs! " + err);
            that.appUpdater.updateError = true;
          }
        );
      } catch (err) {
        alert(err);
        that.appUpdater.updateError = true;
      }
    },
    updateIOS(url) {
      window.open(url, "_blank");
    }
  },
  mounted() {
    if (this.$cordova) {
      this.checkVersion(() => {
        this.appUpdater.isLatest = true;
        this.autoLogin();
      });
    } else {
      this.appUpdater.version = "";
      this.appUpdater.isLatest = true;

      this.autoLogin();
    }
  }
};
</script>

<style lang="scss" scoped>
.login {
  height: 100%;
  background: #c45050;
  background: linear-gradient(to bottom, #7a1811, #f06260);
}
.mint-cell {
  background-color: transparent;
  border-bottom: 1px solid #d0d0d0;
}
.mint-cell-title {
  display: none;
}
.mint-button {
  margin-top: 30px;
  background-color: #f06260;
  border-radius: 20px;
  cursor: pointer;
}
.mint-button:hover {
  background-color: #f04f4d;
}
h3 {
  display: none;
}
.logo-login {
  height: 200px;
  background: url(../assets/logo_login.png) center bottom no-repeat;
}
.wrapper {
  width: 80%;
  margin: 0 auto;
}
svg {
  float: left;
  position: relative;
  top: 32px;
  left: 12px;
  color: #fff;
}
.fa-user {
  top: 44px;
}
.wrapper .mint-cell {
  width: 100%;
}
.wrapper .mint-cell.user-id {
  top: 12px;
}
</style>
<style lang="scss">
.login .mint-field-core {
  background-color: transparent;
  padding-left: 35px;
  color: #fff;
}
.login input::-webkit-input-placeholder {
  color: #d0d0d0;
}
.login input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #d0d0d0;
}
.login input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #d0d0d0;
}
.login input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #d0d0d0;
}
.mint-msgbox-confirm {
  color: #a9362d;
}
.update-class {
  font-size: 18px;
  text-decoration: none;
  color: white;
  padding-top: 10px;
}
</style>
